import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { v4 as uuidv4 } from "uuid";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { solid, regular } from "@fortawesome/fontawesome-svg-core/import.macro";
import { getContacts } from "../../../redux/contacts/contactReducer";
import ContactCard from "../../../Components/ContactCard/ContactCard";

export default function AllContacts() {
  const { contacts, favoriteContacts } = useSelector(state => ({
    ...state.contactReducer
  }));

  const dispatch = useDispatch();

  useEffect(() => {
    if (contacts.length === 0) {
      dispatch(getContacts());
    }
  }, []);

  const addFav = item => {
    dispatch({
      type: "ADDFAV",
      payload: item
    });
  };

  const removeFav = item => {
    dispatch({
      type: "REMOVEFAV",
      payload: item
    });
  };

  return (
    <>
      {contacts.map(item => {
        return (
          <ContactCard key={uuidv4()}>
            <div className="user-info">
              <div className="card-user-round">
                <p>{item.username}</p>
              </div>
              <p>{item.name}</p>
              <p>
                <a href={`mailto:${item.email}`}>{item.email}</a>
              </p>
            </div>

            <div className="user-bottom">
              <Link
                to={{
                  pathname: `contact/${item.username}`
                }}
                state={item}
              >
                Dtails...
              </Link>

              {favoriteContacts.includes(item) ? (
                <FontAwesomeIcon
                  icon={solid("star")}
                  className="star-full"
                  onClick={() => removeFav(item)}
                />
              ) : (
                <FontAwesomeIcon
                  icon={regular("star")}
                  className="star-empty"
                  onClick={() => addFav(item)}
                />
              )}
            </div>
          </ContactCard>
        );
      })}
    </>
  );
}
