const INITIAL_STATE = {
  contacts: [],
  favoriteContacts: []
};

function contactReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case "LOADCONTACTS": {
      const filteredContacts = [];
      for (let i = 0; i < action.payload.length; i++) {
        if ([1, 3, 4, 7, 9].includes(action.payload[i].id)) {
          filteredContacts.push(action.payload[i]);
        }
      }

      return {
        contacts: action.payload,
        favoriteContacts: filteredContacts
      };
    }

    case "ADDCONTACT": {
      const newArrContacts = [...state.contacts];
      newArrContacts.unshift(action.payload);

      return {
        contacts: newArrContacts,
        favoriteContacts: [...state.favoriteContacts]
      };
    }

    case "ADDFAV": {
      const newArrFav = [...state.favoriteContacts];
      newArrFav.push(action.payload);

      return {
        contacts: [...state.contacts],
        favoriteContacts: newArrFav
      };
    }

    case "REMOVEFAV": {
      const toRemove = action.payload;
      const newArrFav = [...state.favoriteContacts];
      for (let i = 0; i < [...state.favoriteContacts].length; i++) {
        if (toRemove === [...state.favoriteContacts][i]) {
          newArrFav.splice(i, 1);
          break;
        }
      }

      return {
        contacts: [...state.contacts],
        favoriteContacts: newArrFav
      };
    }
  }

  return state;
}

export default contactReducer;

export const getContacts = () => dispatch => {
  fetch("https://jsonplaceholder.typicode.com/users")
    .then(response => response.json())
    .then(data => {
      dispatch({
        type: "LOADCONTACTS",
        payload: data
      });
    });
};
