import React, { useState, useRef } from "react";
import { useDispatch } from "react-redux";
import "./Form.css";

export default function Form() {
  const [username, setUsername] = useState({
    username: ""
  });

  const handleUsername = e => {
    const newUsernameState = {
      username: e.target.value
    };
    setUsername(newUsernameState);
  };

  const inputsRef = useRef([]);
  const addRef = el => {
    if (el && !inputsRef.current.includes(el)) {
      inputsRef.current.push(el);
    }
  };

  const dispatch = useDispatch();
  const handleForm = e => {
    e.preventDefault();

    const infosContact = {
      name: inputsRef.current[0].value,
      phone: inputsRef.current[1].value,
      email: inputsRef.current[2].value,
      website: inputsRef.current[3].value,
      company: {
        name: inputsRef.current[4].value
      }
    };

    const newContact = {
      ...username,
      ...infosContact
    };

    dispatch({
      type: "ADDCONTACT",
      payload: newContact
    });

    setUsername({
      username: ""
    });
    e.target.reset();
  };

  return (
    <>
      <h1>Nouveau contact : {username.username}</h1>
      <form onSubmit={handleForm}>
        <label htmlFor="username">Username</label>
        <input
          type="text"
          id="username"
          placeholder="Nom d'utilisateur"
          value={username.username}
          onChange={handleUsername}
        />

        <label htmlFor="name">Prnom Nom</label>
        <input type="text" id="name" placeholder="Prnom et Nom" ref={addRef} />

        <label htmlFor="phone">Tlphone</label>
        <input
          type="text"
          id="phone"
          placeholder="N de tlphone"
          ref={addRef}
        />

        <label htmlFor="email">Email</label>
        <input
          type="text"
          id="email"
          placeholder="Adresse email"
          ref={addRef}
        />

        <label htmlFor="website">Site web</label>
        <input
          type="text"
          id="website"
          placeholder="URL du site web"
          ref={addRef}
        />

        <label htmlFor="company-name">Entreprise</label>
        <input
          type="text"
          id="company-name"
          placeholder="Nom de l'entreprise"
          ref={addRef}
        />

        <button className="btn-add-contact">Ajouter</button>
      </form>
    </>
  );
}
