import React from "react";
import { Link, useLocation } from "react-router-dom";
import "./Contact.css";

export default function Contact() {
  const location = useLocation();

  return (
    <div className="contact-content">
      {location.pathname.includes("/favoris/") ? (
        <Link to="/favoris">
          <button className="btn-return">Retour</button>
        </Link>
      ) : (
        <Link to="/">
          <button className="btn-return">Retour</button>
        </Link>
      )}

      <div className="contact-detail-content">
        <div className="detail-user-round">
          <p>{location.state.username}</p>
        </div>
        <div className="detail-user-info">
          <div>
            <p className="detail-name">{location.state.name}</p>
            <p>{location.state.company.name}</p>
          </div>
          <div>
            <p>
              <a href={`tel:+${location.state.phone}`}>
                {location.state.phone}
              </a>
            </p>
            <p>
              <a href={`mailto:${location.state.email}`}>
                {location.state.email}
              </a>
            </p>
            <p>
              <a href={`http://${location.state.website}`}>
                {location.state.website}
              </a>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
