import { Routes, Route } from "react-router-dom";
import "./App.css";
import Navbar from "./Components/Navbar/Navbar";
import Home from "./Container/Home/Home";
import AddContact from "./Container/AddContact/AddContact";
import Contact from "./Container/Contact/Contact";
import AllContacts from "./Container/Home/AllContacts/AllContacts";
import FavoriteContacts from "./Container/Home/FavoriteContacts/FavoriteContacts";

function App() {
  return (
    <div className="App">
      <Navbar />
      <Routes>
        <Route path="/" element={<Home />}>
          <Route path="/" element={<AllContacts />} />
          <Route path="/favoris" element={<FavoriteContacts />} />
        </Route>
        <Route path="/creer-contact" element={<AddContact />} />
        <Route path="/contact/:slug" element={<Contact />} />
        <Route path="/favoris/contact/:slug" element={<Contact />} />
      </Routes>
    </div>
  );
}

export default App;
